import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Atv_exec_manutencao } from '../../../modelo/Atv_exec_manutencao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'atv_exec_manutencao-dados-edit',   templateUrl: 'atv_exec_manutencao_dadosEdit.component.html' }) export class Atv_exec_manutencaoDadosEditComponent implements OnInit {    atv_exec_manutencao: Atv_exec_manutencao;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.atv_exec_manutencao.Atv_exec_manutencao";                 let id = +params['id'];         this.atv_exec_manutencao = new Atv_exec_manutencao();         this.atv_exec_manutencao.cd_atv_exec_manutencao = id;         o.objetoJson = this.atv_exec_manutencao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a atv_exec_manutencao. ';       }      this.atv_exec_manutencao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


